@charset "UTF-8";
*,
*:after,
*:before {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
}

body {
  color: #333;
  font-family: 'Roboto', 'Helvetica Neue', 'Helvetica', 'Arial', sans-serif;
  font-size: 1.6em;
  font-weight: 300;
  letter-spacing: .01em;
  line-height: 1.6;
}

/*
---
name: Button
category: Components
---

Buttons can be used with `<button>` and `<input>` elements.

Types of buttons:
- Default: Standard button
- Outline: outline button use as a secondary button
- Link: Link Button without background

```types.html

<h1>Default Buttons</h1>
			<button class="button-primary">
				تفعي الإشتراك
			</button>

			<button class="button-link">
				تفعي الإشتراك
			</button>

			<br>
			<br>

			<h1>Size Variants</h1>

			<button class="button button-large">
				تفعي الإشتراك
			</button>

			<button class="button button-medium">
				تفعي الإشتراك
			</button>

			<button class=" button button-small">
				تفعي الإشتراك
			</button>

			<br>
			<br>
			<h1>Link Buttons</h1>

			<button class="button button-start">

				أحس
			</button>
			<button class="button button-continue">

				أحس
			</button>
			<button class="button button-review">

				أحس
			</button>

```
*/
.btn,
button,
input[type='button'],
input[type='reset'],
input[type='submit'] {
  background-color: #1bd8e5;
  background: linear-gradient(-45deg, #01c3f9, #40f6c8);
  border: .1rem solid transparent;
  border-radius: .4rem;
  color: #fff;
  cursor: pointer;
  display: inline-block;
  font-size: 1.1rem;
  font-weight: 700;
  height: 3.8rem;
  letter-spacing: .1rem;
  line-height: 3.8rem;
  padding: 0 3.0rem;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  white-space: nowrap;
  margin: 0 5px;
}

.btn:last-child, .btn:first-child,
button:last-child,
button:first-child,
input[type='button']:last-child,
input[type='button']:first-child,
input[type='reset']:last-child,
input[type='reset']:first-child,
input[type='submit']:last-child,
input[type='submit']:first-child {
  margin: 0;
}

.btn:focus, .btn:hover,
button:focus,
button:hover,
input[type='button']:focus,
input[type='button']:hover,
input[type='reset']:focus,
input[type='reset']:hover,
input[type='submit']:focus,
input[type='submit']:hover {
  background-color: #1bd8e5;
  background: linear-gradient(-45deg, #01c3f9, #40f6c8);
  color: #fff;
  outline: 0;
}

.btn[disabled],
button[disabled],
input[type='button'][disabled],
input[type='reset'][disabled],
input[type='submit'][disabled] {
  cursor: default;
  opacity: .5;
}

.btn[disabled]:focus, .btn[disabled]:hover,
button[disabled]:focus,
button[disabled]:hover,
input[type='button'][disabled]:focus,
input[type='button'][disabled]:hover,
input[type='reset'][disabled]:focus,
input[type='reset'][disabled]:hover,
input[type='submit'][disabled]:focus,
input[type='submit'][disabled]:hover {
  background-color: #1bd8e5;
  background: linear-gradient(-45deg, #01c3f9, #40f6c8);
}

.btn.btn-outline,
button.btn-outline,
input[type='button'].btn-outline,
input[type='reset'].btn-outline,
input[type='submit'].btn-outline {
  background-color: transparent;
  background: none;
  border-color: #1bd8e5;
  color: #1bd8e5;
}

.btn.btn-outline:focus, .btn.btn-outline:hover,
button.btn-outline:focus,
button.btn-outline:hover,
input[type='button'].btn-outline:focus,
input[type='button'].btn-outline:hover,
input[type='reset'].btn-outline:focus,
input[type='reset'].btn-outline:hover,
input[type='submit'].btn-outline:focus,
input[type='submit'].btn-outline:hover {
  background-color: transparent;
  background: none;
  border-color: #1bd8e5;
  color: #1bd8e5;
}

.btn.btn-outline[disabled]:focus, .btn.btn-outline[disabled]:hover,
button.btn-outline[disabled]:focus,
button.btn-outline[disabled]:hover,
input[type='button'].btn-outline[disabled]:focus,
input[type='button'].btn-outline[disabled]:hover,
input[type='reset'].btn-outline[disabled]:focus,
input[type='reset'].btn-outline[disabled]:hover,
input[type='submit'].btn-outline[disabled]:focus,
input[type='submit'].btn-outline[disabled]:hover {
  border-color: inherit;
  color: #1bd8e5;
}

.btn.btn-link,
button.btn-link,
input[type='button'].btn-link,
input[type='reset'].btn-link,
input[type='submit'].btn-link {
  background-color: transparent;
  background: none;
  border-color: transparent;
  color: #1bd8e5;
}

.btn.btn-link:focus, .btn.btn-link:hover,
button.btn-link:focus,
button.btn-link:hover,
input[type='button'].btn-link:focus,
input[type='button'].btn-link:hover,
input[type='reset'].btn-link:focus,
input[type='reset'].btn-link:hover,
input[type='submit'].btn-link:focus,
input[type='submit'].btn-link:hover {
  background-color: transparent;
  background: none;
  border-color: transparent;
  color: #1bd8e5;
}

.btn.btn-link[disabled]:focus, .btn.btn-link[disabled]:hover,
button.btn-link[disabled]:focus,
button.btn-link[disabled]:hover,
input[type='button'].btn-link[disabled]:focus,
input[type='button'].btn-link[disabled]:hover,
input[type='reset'].btn-link[disabled]:focus,
input[type='reset'].btn-link[disabled]:hover,
input[type='submit'].btn-link[disabled]:focus,
input[type='submit'].btn-link[disabled]:hover {
  color: #1bd8e5;
}

button {
  border: none;
}

.button-primary {
  background: linear-gradient(-45deg, #01c3f9, #40f6c8);
  border-radius: 7px;
  padding: .5em 1.5em;
  color: white;
  display: inline-block;
  width: auto;
  text-align: center;
