  /* Opera 11.10+ */
  background-image: linear-gradient(-45deg, #01c3f9, #40f6c8);
  filter: progid:DXImageTransform.Microsoft.gradient(GradientType=0, StartColorStr='#01c3f9', EndColorStr='#40f6c8');
