/*
---
name: Avatars
category: Components
---

Squarckle Images can be simply used with `<div class="squarckle">`.
Dimension of squarckle can be entered in css


```types.html
<div class="squarckle">
	<a href="#"></a>
</div>

<div class="squarckle2">
	<a href="#"></a>
</div>
        

```
*/
.squarckle a {
  height: 150px;
  width: 150px;
  background-image: url("../assets/group-14553@3x.png");
  background-size: cover;
  text-decoration: none;
  color: white;
  display: inline-block;
  margin: 20px;
  border-radius: 24px;
}

.squarckle a:before,
.squarckle a:after {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: inherit;
  border-radius: 100%;
  -webkit-transform: scaleX(2) scaleY(1.05);
  transform: scaleX(2) scaleY(1.05);
  clip: rect(0, 66px, 100px, 34px);
  z-index: -1;
}

.squarckle a:after {
  -webkit-transform: scaleY(2) scaleX(1.05) rotate(90deg);
  transform: scaleY(2) scaleX(1.05) rotate(90deg);
}

.squarckle2 a {
  height: 200px;
  width: 200px;
  background-image: url("../assets/group-14553@3x.png");
  background-size: cover;
  text-decoration: none;
  color: white;
  display: inline-block;
  margin: 20px;
  border-radius: 24px;
}

.squarckle2 a:before,
.squarckle2 a:after {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: inherit;
  border-radius: 100%;
  -webkit-transform: scaleX(2) scaleY(1.05);
  transform: scaleX(2) scaleY(1.05);
  clip: rect(0, 66px, 100px, 34px);
  z-index: -1;
}

.squarckle2 a:after {
  -webkit-transform: scaleY(2) scaleX(1.05) rotate(90deg);
  transform: scaleY(2) scaleX(1.05) rotate(90deg);
}

/*
---
name: Forms
category: Components
---

Normal Forms can be used with `<div class="form-item">` by putting `<input>` element inside the div.


```types.html
            <h3>Default</h3>
            <div class="form-item">
				<input type="text" name="name" class="input">
            </div>
            
            <h3>Toggle</h3>
			<div class="form-item form-toggle">
				<span class="v-line"></span>
				<input type="text" name="name" class="input active">
            </div>
            
            <h3>Phone</h3>
			<div class="form-item phone">
				<img src="assets/group-14551@3x.png" />
				<span class="v-line"></span>
				<input type="text" name="name" class="input">
            </div>
            
            <h3>Expanded</h3>
			<div class="form-item">
                <input type="text" name="name" class="input expanded">
                <div class="expanded-icon">
                <img src="assets/group-14584.svg" />
                </div>
			</div>

            <h3>Verified</h3>
			<div class="form-item ">
				<div class="check-icon-wrapper">
					<img src="assets/group-14576@3x.png" />
				</div>
				<input type="text" name="name" class="input verified">
			</div>

            <h3>Error</h3>
			<div class="form-item ">
				<div class="error-icon-wrapper">
					<img src="assets/group-14578@3x.png" />
				</div>
				<input type="text" name="name" class="input error">
            </div>
            
            <h3>Dropdown</h3>
			<div class="form-item ">
				<div class="dropdown-icon">
					<img src="assets/path.png" />
				</div>
				<input type="text" name="name" class="input">
