  border-radius: 4px;
  height: 38px;
  padding: 6px 10px;
  border: 1px solid #D1D1D1;
  border-radius: 4px;
  box-shadow: none;
  box-sizing: border-box;
}

.form-item input:focus, .form-item input.active, .form-item .input:focus, .form-item .input.active {
  box-shadow: none;
  border: 1px solid #40f6c8;
  outline: none !important;
}

.form-item .expanded {
