}

.form-item .error {
  border-color: #d62739;
}

.form-item .error:focus {
  border: 1px solid #d62739;
}

.form-item .check-icon-wrapper {
  position: absolute;
  top: 0px;
  left: -27px;
}

.form-item .error-icon-wrapper {
  position: absolute;
  top: 0px;
  left: -27px;
}

.form-item img {
  position: absolute;
  top: 10px;
  height: 18px;
  left: 20px;
  z-index: 1;
