}

.form-item .dropdown-icon {
  position: absolute;
  top: 6px;
  left: -4px;
}

.form-item .dropdown-icon img {
  height: auto;
}

/*
---
name: Toggle
category: Components
---

Buttons can be used with `<button>` and `<input>` elements.

Types of buttons:
- Default: Standard button
- Outline: outline button use as a secondary button
- Link: Link Button without background

```types.html
<br>
<div class="toggle">
				<div class="display">
					<label class="label toggle">
						<input type="checkbox" class="toggle_input" />
						<div class="toggle-control"></div>
					</label>
				</div>
            </div>
            <br>

```
*/
.toggle {
  position: relative;
}

.toggle .display {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.toggle .display .toggle .toggle-control {
  transition: 0.2s cubic-bezier(0.95, 0.05, 0.795, 0.035);
  width: 160px;
  height: 40px;
  display: block;
  border: 2px solid #d1d1d1;
  border-radius: 10px;
  background-color: rgba(0, 0, 0, 0.06);
  position: relative;
}

.toggle .display .toggle .toggle-control:after {
  transition: 0.2s cubic-bezier(0.95, 0.05, 0.795, 0.035);
  content: "";
  width: 76.19048px;
  height: 35.08772px;
  display: block;
  background-color: #fff;
  border-radius: 5px;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.4), 0 3px 2px rgba(0, 0, 0, 0.4);
  position: absolute;
  top: 0;
  left: 0;
}

.toggle .display .toggle input {
  display: none;
}

.toggle .display .toggle input:checked + .toggle-control {
  background: linear-gradient(-45deg, #01c3f9, #40f6c8);
  border-color: #40f6c8;
}

.toggle .display .toggle input:checked + .toggle-control:after {
  left: 80px;
}

