/*
---
name: Modal
category: Components
---



```types.html
<div class="modal-wrapper">
			<div class="lock-icon">
				<img src="assets/group-14438.svg" />
			</div>
			<div class="modal-content">
				<p>٢٥/٠ ٠٪ هذا المحتوي متاح فقط من خلال عضويات نو</p>
