}

.modal-wrapper .lock-icon img {
  width: 100px;
  height: auto;
}

.modal-wrapper .modal-content {
  height: 200px;
  width: 400px;
  border: 1px solid #d1d1d1;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  background: white;
  text-align: center;
}

.modal-wrapper .modal-buttons {
  position: absolute;
  width: 400px;
  left: 66%;
  transform: translate(-50%, -50%);
}

.modal-wrapper .modal-buttons .modal-buttons-wrappers {
  display: inline-block;
}

.modal-wrapper .modal-buttons .modal-buttons-wrappers .button-link {
